body {
  background-color: rgb(253, 253, 253);
}
.codeboard-container {
  display: flex;
  flex-direction: column;
  height: 100vh;
  max-width: 1500px;
  overflow: hidden;
  margin: 10px;
  padding: 10px;
  box-sizing: border-box;
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #f1f1f1;
  padding: 10px;
}

.navbar-left {
  display: flex;
  align-items: center;
}

.dropdown {
  position: relative;
  display: inline-block;
}

.dropdown-btn {
  background-color: #4caf50;
  color: white;
  padding: 8px;
  border: none;
  cursor: pointer;
}

.dropdown-content {
  position: absolute;
  background-color: white;
  min-width: 160px;
  z-index: 1;
  display: none;
  white-space: nowrap; /* Prevent line breaks */
}

.dropdown-option {
  align-items: center;
  color: black;
  padding: 3px 5px;
  text-decoration: none;
  display: inline-block; /* Display options in a line */
  cursor: pointer;
  justify-content: center;
  margin-right: 5px; /* Add some spacing between options */
}


.dropdown-option:hover {
  background-color: #f1f1f1;
}

.dropdown:hover .dropdown-content {
  display: block;
}

.navbar-btn {
  background-color: #008cba;
  color: white;
  padding: 8px;
  border: none;
  cursor: pointer;
  margin-left: 10px;
}

.code-area {
  flex: 1 1;
  padding: 10px;
  background-color: #f5f5f5;
  border: 1px solid #ddd;
}


pre {
  margin: 0;
}

.input-container {
  display: flex;
  flex-direction: column;
  padding: 10px;
}

.input-label {
  margin-right: 0px;
}

.input-box {
  flex: 1 1;
  padding: 5px;
  box-sizing: border-box;
  border: 1px solid #ddd;
  resize: none;
}
.output-container {
  padding: 10px;
  background-color: #fff;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.output-title {
  margin-bottom: 10px;
}

.output-box {
  width: 100%;
  height: 100%;
  padding: 5px;
  background-color: #f1f1f1;
  overflow-y: auto;
}

.code-input {
  font-size: 16px; /* Increase the font size */
  height: 245px; /* Increase the height of the code input area */
  width: 1230px;
  padding: 10px;
  background-color: #f5f5f5;
  border: 1px solid #ddd;
}

body {
  margin: 0px;
  padding: 0;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  padding-left: 0px;
}
.form-container {
  max-width: 300px;
  margin: 20px;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.form-container h2 {
  text-align: center;
  margin-bottom: 20px;
  margin-top: 20px;
}

.form-container label {
  display: block;
  margin-bottom: 10px;
  margin-top: 20px;
}

.form-container input[type="email"],
.form-container input[type="password"] {
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 3px;
  margin-bottom: 15px;
}

.form-container button[type="submit"] {
  background-color: #4caf50;
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 3px;
  cursor: pointer;
  width: 100%;
}


